export const catalogBatchProcess = (event) => {
  console.log('catalogBatchProcess ', event);

  const sns = new SNS({ region: 'eu-west-1' });

  const getMessageParams = (product) => {
    console.log('product ', product.replace(/^\uFEFF/gi, ''));
    
    return {
      Subject: 'Hello description',
      Message: JSON.stringify(product),
      TopicArn: process.env.SNS_ARN,
      MessageAttributes: {
        event: {
          DataType: 'String',
          StringValue: 'description',
        },
        description: {
          DataType: 'String',
          StringValue: JSON.parse(product.replace(/^\uFEFF/gi, '')).description
        }
      }
    }
  };

  const products = event.Records.map(async ({ body }) => {
    // await postProduct({ body: body.replace(/\ufeff/gi, '') });
    const messageParams = getMessageParams(body);

    // await sns.publish({
    //   Subject: 'Hello prod',
    //   Message: JSON.stringify(body),
    //   TopicArn: process.env.SNS_ARN,
    // }).promise();

    sns.publish(messageParams, (err, datas) => {
      if (err) {
        console.log('err ', err);
      }
      console.log('dtas  ', datas);
      
    });
    return body;
  });
}

export const main = middyfy(catalogBatchProcess);
